<template>
  <div id="scircle">
    <div class="scircle-content">
      <div class="scircle-left">
        <div class="circle-left"></div>
      </div>
      <div class="scircle-right">
        <div class="circle-right"></div>        
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Scircle'
}
</script>

<style scoped>
.scircle-content{
  width: 50px;
  height: 50px;
  position: relative;
}
.scircle-left{
  width: 25px;
  height: 50px;
  position: absolute;  
  overflow: hidden;
  top:0;
  left: 0;
}

.scircle-right{
  width: 25px;
  height: 50px;
  position: absolute;
  overflow: hidden;
  top:0;  
  right: 0; 
}
.circle-left{
  top: 0;
  left: 0;
  position: absolute;
  width: 40px;
  height: 40px;
  border: 5px solid red;
  border-radius: 50%;  
  border-top: 5px solid transparent;
  transform: rotate(225deg);  
  border-right: 5px solid transparent;
  animation:runLeft 5s linear infinite;
}
.circle-right{
  top:0;
  right: 0;
  position: absolute;  
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 5px solid red;
  border-bottom: 5px solid transparent;
  border-left: 5px solid transparent;
  transform: rotate(225deg);
  animation:runRight 5s linear infinite;
}
@keyframes runLeft{
  0%{
     transform: rotate(-135deg);
  }
  50%{
     transform: rotate(-135deg);    
  }
  100%{
     transform: rotate(45deg);        
  }
}

@keyframes runRight{
  0%{
     transform: rotate(-135deg);
  }
  50%{
     transform: rotate(45deg);    
  }
  100%{
     transform: rotate(45deg);        
  }
}
</style>




